import React, { Component } from "react";

class AdminMain extends Component {
  render() {
    const { onSetPost, posts, tags } = this.props;

    return (
      <div>
        <h2>Admin</h2>
        {posts.map(post => {
          return (
            <div key={post.id} onClick={() => onSetPost(post)}>
              {post.title}
            </div>
          );
        })}
      </div>
    );
  }
}

export default AdminMain;
